#loadingIcon {
  position: fixed;
  left:50%;
  top:50%;
  transform:translate(-50%, -50%);
  width: 100px;
  height:100px;
  z-index:50;
  background-size:cover;
  background-repeat: no-repeat;
  opacity:1;
  pointer-events: none;
  mix-blend-mode: lighten;
}
#loadingIcon:after {
  content: 'Loading Data...';
  color:black;
  font-weight: bold;
  position: absolute;
  bottom:-30px;
  text-align: center;
  width:100%;
  background:white;
}
